import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import moment from 'moment';
import { createChart } from 'lightweight-charts';
import './App.css';
import Login from './components/Login';

function App() {
  const [dollarData, setDollarData] = useState([]);
  const [showChart, setShowChart] = useState(true);
  const [isCandles, setIsCandles] = useState(true);
  const [timeInterval, setTimeInterval] = useState('15m');
  const chartContainerRef = useRef();
  const chartRef = useRef();
  const seriesRef = useRef();
  const rsiRef = useRef();
  const rsiLength = 14;
  const rsiChartRef = useRef();
  const rsiContainerRef = useRef();
  const [token, setToken] = useState(localStorage.getItem('accessToken') || null);
  const [isAuthenticated, setIsAuthenticated] = useState(!!localStorage.getItem('accessToken'));

  const handleResize = () => {
    if (chartRef.current) {
      const container = chartContainerRef.current;
      const width = container.clientWidth;
      chartRef.current.applyOptions({
        width: width,
        height: window.innerHeight * 0.7,
      });
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const baseUrl = window.location.hostname === 'localhost' 
          ? 'http://localhost:3000'
          : 'https://itsmetony.duckdns.org';

        const token = localStorage.getItem('accessToken') || null;
        
        const response = await axios.get(`${baseUrl}/api/v1/dollar/history`, {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*',
            'Authorization': `Bearer ${token}`
          },
          withCredentials: false,
          proxy: {
            host: 'localhost',
            port: 3000
          }
        });
        
        const sortedData = response.data.sort((a, b) => 
          new Date(a.datetime) - new Date(b.datetime)
        );
        setDollarData(sortedData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    fetchData();
  }, []);

  const prepareCandleData = (data, interval) => {
    const groupedData = {};
    const intervalMinutes = {
      '1m': 1,
      '5m': 5,
      '15m': 15,
      '1h': 60
    }[interval];

    // Primero ajustamos la hora y ordenamos
    const sortedData = [...data]
      .map(item => ({
        ...item,
        adjustedTime: moment(item.datetime).subtract(3, 'hours').valueOf()
      }))
      .sort((a, b) => a.adjustedTime - b.adjustedTime);

    sortedData.forEach(record => {
      const timestamp = moment(record.adjustedTime);
      const roundedMinutes = Math.floor(timestamp.minutes() / intervalMinutes) * intervalMinutes;
      const timeKey = timestamp
        .minutes(roundedMinutes)
        .seconds(0)
        .unix();

      if (!groupedData[timeKey]) {
        groupedData[timeKey] = {
          open: record.value,
          high: record.value,
          low: record.value,
          close: record.value,
          time: timeKey
        };
      } else {
        groupedData[timeKey].high = Math.max(groupedData[timeKey].high, record.value);
        groupedData[timeKey].low = Math.min(groupedData[timeKey].low, record.value);
        groupedData[timeKey].close = record.value;
      }
    });

    return Object.values(groupedData).sort((a, b) => a.time - b.time);
  };

  const prepareLineData = (data, interval) => {
    const groupedData = {};
    const intervalMinutes = {
      '1m': 1,
      '5m': 5,
      '15m': 15,
      '1h': 60
    }[interval];

    // Primero ajustamos la hora y ordenamos
    const sortedData = [...data]
      .map(item => ({
        ...item,
        adjustedTime: moment(item.datetime).subtract(3, 'hours').valueOf()
      }))
      .sort((a, b) => a.adjustedTime - b.adjustedTime);

    sortedData.forEach(record => {
      const timestamp = moment(record.adjustedTime);
      const roundedMinutes = Math.floor(timestamp.minutes() / intervalMinutes) * intervalMinutes;
      const timeKey = timestamp
        .minutes(roundedMinutes)
        .seconds(0)
        .unix();

      groupedData[timeKey] = {
        time: timeKey,
        value: record.value
      };
    });

    return Object.values(groupedData).sort((a, b) => a.time - b.time);
  };

  // Funcin para calcular el RSI
  const calculateRSI = (data) => {
    if (!data || data.length === 0) return [];

    let prices = [];
    if (isCandles) {
      prices = data.map(d => d.close);
    } else {
      prices = data.map(d => d.value);
    }

    let gains = [0];
    let losses = [0];

    for (let i = 1; i < prices.length; i++) {
      const difference = prices[i] - prices[i - 1];
      gains.push(difference > 0 ? difference : 0);
      losses.push(difference < 0 ? Math.abs(difference) : 0);
    }

    const rsiData = [];
    let avgGain = gains.slice(0, rsiLength + 1).reduce((a, b) => a + b) / rsiLength;
    let avgLoss = losses.slice(0, rsiLength + 1).reduce((a, b) => a + b) / rsiLength;

    for (let i = rsiLength; i < prices.length; i++) {
      if (i > rsiLength) {
        avgGain = (avgGain * (rsiLength - 1) + gains[i]) / rsiLength;
        avgLoss = (avgLoss * (rsiLength - 1) + losses[i]) / rsiLength;
      }

      const rs = avgGain / avgLoss;
      const rsi = 100 - (100 / (1 + rs));
      
      rsiData.push({
        time: data[i].time,
        value: rsi
      });
    }

    return rsiData;
  };

  useEffect(() => {
    if (!showChart || !dollarData.length || !chartContainerRef.current) return;

    if (!chartRef.current) {
      const container = chartContainerRef.current;
      const width = container.clientWidth;

      const mainChart = createChart(container, {
        width: width,
        height: window.innerHeight * 0.4,
        layout: {
          background: { color: '#ffffff' },
          textColor: '#333',
        },
        grid: {
          vertLines: { color: '#f0f0f0' },
          horzLines: { color: '#f0f0f0' },
        },
        timeScale: {
          timeVisible: true,
          secondsVisible: false,
          rightOffset: 12,
          fixLeftEdge: true,
          fixRightEdge: true,
          barSpacing: 6,
          tickMarkFormatter: (time) => {
            const date = new Date(time * 1000);
            return moment(date).format('HH:mm');
          },
        },
        rightPriceScale: {
          autoScale: false,
          scaleMargins: {
            top: 0.1,
            bottom: 0.1,
          },
        },
        localization: {
          timeFormatter: (time) => {
            const date = new Date(time * 1000);
            return moment(date).format('DD/MM/YYYY HH:mm');
          },
        },
      });

      chartRef.current = mainChart;

      if (rsiContainerRef.current && !rsiChartRef.current) {
        const rsiChart = createChart(rsiContainerRef.current, {
          width: width,
          height: window.innerHeight * 0.25,
          layout: {
            background: { color: '#ffffff' },
            textColor: '#333',
          },
          grid: {
            vertLines: { color: '#f0f0f0' },
            horzLines: { color: '#f0f0f0' },
          },
          timeScale: {
            timeVisible: true,
            secondsVisible: false,
            rightOffset: 10,
            fixLeftEdge: true,
            fixRightEdge: true,
            lockVisibleTimeRangeOnResize: true,
            barSpacing: 6,
            tickMarkFormatter: (time) => {
              const date = new Date(time * 1000);
              return moment(date).format('HH:mm');
            },
          },
          rightPriceScale: {
            autoScale: true,
            scaleMargins: {
              top: 0.2,
              bottom: 0.2,
            },
          },
        });

        // Agregar lneas horizontales para los niveles 30 y 70
        const level30Line = rsiChart.addLineSeries({
          color: '#ef5350',
          lineWidth: 1,
          lineStyle: 2,
        });

        const level70Line = rsiChart.addLineSeries({
          color: '#26a69a',
          lineWidth: 1,
          lineStyle: 2,
        });

        // Establecer los datos para las lneas horizontales usando el rango de datos actual
        const data = isCandles ? 
          prepareCandleData(dollarData, timeInterval) : 
          prepareLineData(dollarData, timeInterval);

        if (data && data.length > 0) {
          const timeRange = {
            from: data[0].time,
            to: data[data.length - 1].time
          };

          level30Line.setData([
            { time: timeRange.from, value: 30 },
            { time: timeRange.to, value: 30 }
          ]);

          level70Line.setData([
            { time: timeRange.from, value: 70 },
            { time: timeRange.to, value: 70 }
          ]);
        }

        rsiChartRef.current = rsiChart;
      }

      window.addEventListener('resize', handleResize);
    }

    // Actualizar ambos grficos
    updateChartType();

    // Agregar un pequeo retraso para asegurar que los grficos estn listos
    setTimeout(() => {
      handleAdjustCharts();
    }, 100);

    return () => {
      window.removeEventListener('resize', handleResize);
      if (chartRef.current) {
        chartRef.current.remove();
        chartRef.current = null;
        seriesRef.current = null;
      }
      if (rsiChartRef.current) {
        rsiChartRef.current.remove();
        rsiChartRef.current = null;
        rsiRef.current = null;
      }
    };
  }, [showChart, dollarData, isCandles, timeInterval]);

  const handleToggleView = () => {
    if (chartRef.current) {
      chartRef.current.remove();
      chartRef.current = null;
      seriesRef.current = null;
    }
    setShowChart(!showChart);
  };

  const updateChartType = () => {
    if (!chartRef.current || !dollarData.length) return;

    try {
      // Actualizar grfico principal
      if (seriesRef.current) {
        chartRef.current.removeSeries(seriesRef.current);
      }

      const data = isCandles ? 
        prepareCandleData(dollarData, timeInterval) : 
        prepareLineData(dollarData, timeInterval);

      if (data && data.length > 0) {
        if (isCandles) {
          seriesRef.current = chartRef.current.addCandlestickSeries({
            upColor: '#26a69a',
            downColor: '#ef5350',
            borderVisible: false,
            wickUpColor: '#26a69a',
            wickDownColor: '#ef5350'
          });
        } else {
          seriesRef.current = chartRef.current.addLineSeries({
            color: '#2962FF',
            lineWidth: 2,
          });
        }
        
        seriesRef.current.setData(data);
      }

      // Actualizar RSI para todas las temporalidades
      if (rsiChartRef.current) {
        if (rsiRef.current) {
          rsiChartRef.current.removeSeries(rsiRef.current);
        }

        const rsiData = calculateRSI(data);
        
        rsiRef.current = rsiChartRef.current.addLineSeries({
          color: '#2962FF',
          lineWidth: 2,
          priceFormat: {
            type: 'price',
            precision: 2,
            minMove: 0.01,
          },
        });

        if (rsiData && rsiData.length > 0) {
          rsiRef.current.setData(rsiData);
        }

        // Agregar lneas horizontales para niveles 30 y 70
        const rsiLines = rsiChartRef.current.addLineSeries({
          color: 'rgba(0, 0, 0, 0.3)',
          lineWidth: 1,
          lineStyle: 2, // lnea punteada
        });

        const timeRange = {
          from: Math.min(...rsiData.map(d => d.time)),
          to: Math.max(...rsiData.map(d => d.time))
        };

        rsiLines.setData([
          { time: timeRange.from, value: 30 },
          { time: timeRange.to, value: 30 },
          { time: timeRange.from, value: 70 },
          { time: timeRange.to, value: 70 }
        ]);
      }
    } catch (error) {
      console.error('Error en updateChartType:', error);
    }
  };

  // Primero agregamos la funcin para ajustar los grficos
  const handleAdjustCharts = () => {
    if (chartRef.current && rsiChartRef.current) {
      const mainTimeScale = chartRef.current.timeScale();
      const mainRange = mainTimeScale.getVisibleLogicalRange();
      if (mainRange) {
        rsiChartRef.current.timeScale().setVisibleLogicalRange(mainRange);
      }
    }
  };

  const handleLogin = (newToken) => {
    setToken(newToken);
    setIsAuthenticated(true);
  };

  return (
    <div className="container">
      {!isAuthenticated ? (
        <Login onLogin={handleLogin} />
      ) : (
        <>
          <div className="button-container">
            <button 
              className="trading-view-button"
              onClick={handleToggleView}
            >
              {showChart ? 'Mostrar Tabla' : 'TradingView'}
            </button>
            
            {showChart && (
              <div className="chart-controls">
                <select 
                  className="interval-select"
                  value={timeInterval}
                  onChange={(e) => {
                    setTimeInterval(e.target.value);
                  }}
                >
                  <option value="1m">1 MIN</option>
                  <option value="5m">5 MIN</option>
                  <option value="15m">15 MIN</option>
                  <option value="1h">1 HORA</option>
                </select>
                <button 
                  className="chart-type-button"
                  onClick={() => setIsCandles(!isCandles)}
                >
                  {isCandles ? 'Mostrar Lnea' : 'Mostrar Velas'}
                </button>
              </div>
            )}
          </div>

          {showChart ? (
            <div>
              <div ref={chartContainerRef} className="chart-container" />
              <div>
                <div className="rsi-header">
                  <h3 className="rsi-title">RSI (14)</h3>
                  <button 
                    className="chart-type-button"
                    onClick={handleAdjustCharts}
                  >
                    Ajustar
                  </button>
                </div>
                <div ref={rsiContainerRef} className="chart-container" />
              </div>
            </div>
          ) : (
            <table className="dollar-table">
              <thead>
                <tr>
                  <th>Fecha y Hora</th>
                  <th>Valor (CLP)</th>
                </tr>
              </thead>
              <tbody>
                {[...dollarData].reverse().map((record, index) => {
                  const date = new Date(record.datetime);
                  const localDate = new Date(date.getTime() - (3 * 60 * 60 * 1000));
                  return (
                    <tr key={index}>
                      <td>{moment(localDate).format('DD/MM/YYYY HH:mm')}</td>
                      <td>${record.value.toFixed(2)}</td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          )}
        </>
      )}
    </div>
  );
}

export default App;